@import "../../../Style/constants.scss";

.bar-small {
    height: 3px;
    background-color: $lightOrange;
    border-radius: 4px;

    .active {
        background-color: $orange;
        height: 3px;
        border-radius: 4px;
    }
}

.nutrient {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 8px;

    .name {
        margin: 0px;
        width: 100px;
        text-align: left;
        font-weight: 600;
    }

    .percents {
        width: fit-content;
        gap: 4px;

        .augment {
            color: #779D80;
        }

        .delimeter,
        .total {
            color: #38513D;
        }
    }

    .bar {
        background-color: #F5F5F5;
        width: 180px;
        height: 20px;
        border-radius: 12px;
        overflow: hidden;
        position: relative;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

        div {
            transition: width .5s;
            border-radius: 12px;
        }

        .active {
            background-color: #38513D;
            height: 100%;
            position: absolute;
            top: 0;
        }

        .selectedFood {
            background-color: #779D80;
            height: 100%;
            position: absolute;
            top: 0;
        }
    }

    &.greyed {
        p {
            color: #787878;
            font-weight: 400;
        }

        .bar {
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
        }

        .active {
            background-color: $pear;
        }

        .selectedFood {
            background-color: $lightBlue;
        }
    }
    &.highlighted {
        .active {
            background-color: $orange;
        }

        .selectedFood {
            background-color: $lightOrange;
        }
    }

    &:hover {
        cursor: pointer;

        p {
            font-weight: 600;
        }

        .bar {
            background-color: #dddddd;

            .active {
                background-color: #38513D;
            }

            .selectedFood {
                background-color: #779D80;
            }
        }

        &.greyed {
            .active {
                background-color: #bbbbbb;
            }

            .selectedFood {
                background-color: #cccccc;
            }
        }

        &.highlighted {
            .active {
                background-color: $orange;
            }
    
            .selectedFood {
                background-color: $lightOrange;
            }
        }
    }
}