@import "../../../Style/constants.scss";

.recommendations{
    background-color: $pear;
    border-radius: 16px;
    padding: 24px 36px;
    height: fit-content;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    .card-row {
        gap: 24px;
        width: calc(100% + 80px);
        overflow-x: scroll;
        overflow-y: hidden;
        height: fit-content;
        padding-left: 40px;
        margin-left: -40px;
        margin-right: -40px;
        padding-bottom: 16px;
    }
    .rec-header {
        justify-content: space-between;
        color: white;
        padding-bottom: 20px;
        align-items: center;
        .words {
            gap: 12px;
        }
        .display-settings {
            gap: 8px;
            align-items: center;
        }
    }
}

.no-results {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 148px;
    gap: 12px;
    img {
        height: 84px;
        object-fit: contain;
    }
    p {
        text-align: center;
    }
}